<template>
  <div>
    <!-- table -->
    <vue-good-table
      :columns="columns"
      :rows="boxes"
      :search-options="{
        enabled: true,
        externalQuery: searchTerm,
      }"
      :pagination-options="{
        enabled: false,
      }"
    >
      <template
        slot="table-row"
        slot-scope="props"
      >
        <!-- Column: Location -->
        <div
          v-if="props.column.field === 'location'"
          class="text-nowrap"
        >
          <span class="text-nowrap">{{ boxLocationToReadable(props.row.location) }}</span>
        </div>

        <!-- Column: Key1 -->
        <div
          v-else-if="props.column.field === 'key1'"
          class="text-nowrap"
        >
          <span
            v-b-tooltip.hover.v-primary
            :title="props.row.key1 + ', ' + props.row.key2"
            variant="flat-secondary"
            class="btn-icon"
            size="sm"
          ><feather-icon icon="KeyIcon" />
          </span>
        </div>

        <!-- Column: Good -->
        <div
          v-else-if="props.column.field === 'good_id'"
          class="text-nowrap"
        >
          <span class="text-nowrap">{{
            elementAt("goods", props.row.good_id, "plural")
          }}</span>

          <b-badge
            v-if="elementAt('countries', props.row.good_origin, 'name') !== ''"
            v-b-tooltip
            class="infobadge infoflag"
            :title="elementAt('countries', props.row.good_origin, 'name')"
            variant="light-secondary"
          >
            <!--<feather-icon icon="GlobeIcon" />-->
            <country-flag
              :country="elementAt('countries', props.row.good_origin, 'code')"
              size="small"
              rounded
            />
            <!--<small>{{elementAt("countries", props.row.good_origin, "code")}}</small>-->
          </b-badge>

          <b-badge
            v-if="
              elementAt('goodcategories', props.row.good_category, 'description') !== ''
            "
            v-b-tooltip
            class="infobadge"
            :title="elementAt('goodcategories', props.row.good_category, 'description')"
            variant="light-secondary"
          ><feather-icon icon="AwardIcon" /> <small>{{ props.row.good_category }}</small><!--<span >{{props.row.good_category}}</span>-->
          </b-badge>

          <b-badge
            v-if="props.row.good_price !== null"
            v-b-tooltip
            class="infobadge"
            :title="props.row.good_price + ' '"
            variant="light-secondary"
          ><!--<feather-icon icon="DollarSignIcon" />--> <span>{{ props.row.good_price }}<small> </small></span>
          </b-badge>
        </div>

        <!-- Column: Good Weight -->
        <span v-else-if="props.column.field === 'good_weight'">
          {{ props.row.good_weight }} <small v-if="props.row.good_weight !== null">Kg</small>
        </span>

        <!-- Column: Color -->
        <span v-else-if="props.column.field === 'color'">
          <b-badge :style="'background-color: ' + colorVariant(props.row.color)">
            {{ props.row.color }}
          </b-badge>
        </span>

        <!-- Column: Action -->
        <span v-else-if="props.column.field === 'action'">
          <span>
            <b-dropdown
              variant="link"
              toggle-class="text-decoration-none"
              no-caret
            >
              <template v-slot:button-content>
                <feather-icon
                  icon="MoreVerticalIcon"
                  size="16"
                  class="text-body align-middle mr-25"
                />
              </template>
              <b-dropdown-item>
                <feather-icon
                  icon="Edit2Icon"
                  class="mr-50"
                />
                <span>Edit</span>
              </b-dropdown-item>
              <b-dropdown-item>
                <feather-icon
                  icon="TrashIcon"
                  class="mr-50"
                />
                <span>Delete</span>
              </b-dropdown-item>
            </b-dropdown>
          </span>
        </span>

        <!-- Column: Common -->
        <span v-else>
          {{ props.formattedRow[props.column.field] }}
        </span>
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import {
  BAvatar,
  BBadge,
  BPagination,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BDropdown,
  BDropdownItem,
  BButton,
  VBTooltip,
} from 'bootstrap-vue'
import { VueGoodTable } from 'vue-good-table'
import { elementAt, boxLocationToReadable } from '../utils'

export default {
  components: {
    VueGoodTable,
    BAvatar,
    BBadge,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
    BButton,
  },
  directives: {
    'b-tooltip': VBTooltip,
  },
  data() {
    return {
      boxes: [],
      pageLength: 3,
      dir: false,
      columns: [
        {
          label: 'Codice',
          field: 'code',
          filterOptions: {
            enabled: true,
            placeholder: 'Cerca codice',
          },
        },
        {
          label: 'Colore',
          field: 'color',
          filterOptions: {
            enabled: true,
            placeholder: 'Cerca colore',
          },
        },
        /* {//key2 is added above with a comma
          label: "Chiave 2",
          field: "key2",
          filterOptions: {
            enabled: true,
            placeholder: "Cerca chiave",
          },
        }, */
        {
          label: 'Luogo',
          field: 'location',
          filterOptions: {
            enabled: true,
            placeholder: 'Cerca luogo',
          },
        },
        {
          label: 'Merce',
          field: 'good_id',
          filterOptions: {
            enabled: true,
            placeholder: 'Cerca merce',
          },
        },
        {
          label: 'Peso',
          field: 'good_weight',
          filterOptions: {
            enabled: false,
            /* placeholder: "Cerca luogo", */
          },
        },
        {
          label: 'Chiavi',
          field: 'key1', // key2 is added above with a comma
          filterOptions: {
            enabled: false,
            placeholder: 'Cerca chiavi',
          },
        },
        /* {
          label: "Azioni",
          field: "action",
        }, */
      ],
      searchTerm: '',
    }
  },
  computed: {
    colorVariant() {
      const boxColor = {
        Grigia: '#979797',
        Verde: '#569850',
      }

      return status => boxColor[status]
    },
  },
  beforeCreate() {},
  beforeDestroy() {
    const self = this
    if (self.intervalTimer !== undefined) clearInterval(self.intervalTimer)
  },
  setup() {
    const self = this
    // console.log(this.$store);
  },
  created() {
    const self = this
    self.retrieveData()
    self.intervalTimer = window.setInterval(() => {
      self.retrieveData()
    }, 1000)
  },
  methods: {
    retrieveData() {
      const self = this
      self.$store.dispatch('boxes/get').then(result => {
        console.log(result)
        self.boxes = result.data.data
      })
    },
    boxLocationToReadable,
    elementAt,
    capitalize(text) {
      if (text === undefined) return ''
      return text
        .toLowerCase()
        .split(' ')
        .map(s => s.charAt(0).toUpperCase() + s.substring(1))
        .join(' ')
    },
  },
}
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-good-table.scss";

.infobadge {
  margin-left: 3px;
}

.infobadge.infoflag .small-flag {
  margin: -1.2em -1.5em -1.2em;
}
</style>
